.profile__img {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  opacity: 0.7;
}
.profile {
  color: #595959;
  width: 60%;
  margin: 0 auto 100px auto;
  position: relative;
}
.profile__user {
  font-size: 1.5em;
}
.profile__user-info {
  margin: 40px 0 0 25px;
}
.profile__counter-uploaded > img {
  height: auto;
  width: auto;
}
.profile__counter-like > img {
  height: auto;
  width: auto;
}
.profile__card {
  position: relative;
  margin-top: 20px;
  display: flex;
}
.profile__button {
  border-color: #f9f9f9;
  font-size: 1.2em;
  color: #7793f4;
  border: 2px solid;
  background-color: #f9f9f9;
  border-radius: 25px;
  padding: 10px 15px;
  margin: 10px auto;
  text-decoration: none;
  width: 250px;
}
.profile__button.exit {
  position: absolute;
  top: 0;
  right: 0;
  width: auto;
}
.profile__like-photo {
  max-height: 350px;
}
.profile__like {
  color: #595959;
}

.swiper-slide {
  height: auto !important;
}

@media (max-width: 375px) {
.profile__like-photo {
    max-height: 150px;
}
}
@media (max-width: 768px) {
  .profile__like > h2 {
    margin: 10px;
  }
  .profile__details{
      margin: 10px;
  }
  .profile__card {
    margin-top: 0px;
    color: #f9f9f9;
  }
  .profile {
    width: 100%;
  }
  .profile__about {
    color: #595959;
    margin: 10px;
  }
  .profile__img {
    width: 100%;
    height: 300px;
    opacity: 0.7;
    border-radius: 0%;
  }
  .profile__user-info {
    position: absolute;
    top: 10%;
    left: 10%;
  }
  :root {
    --swiper-navigation-size: 34px !important;
  }
}
